import axios from "axios";

/**
 * get
 * @param url  url
 * @param params  
 * @returns {Promise}
 */
export function get(url, params = {}) {
    return new Promise((resolve, reject) => {
        axios.get(url, {params: params,}).then((response) => {
            const _res = JSON.parse(JSON.stringify(response.data));
            if(_res!=null){
                if(_res['code']===200||_res['status']==="success"){
                    return resolve(_res);
                }
            }
            return reject(_res);
        }).catch((error) => {
            // console.log(error);
            reject(error);
        });
    });
}

/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */
export function post(url, _data) {
    return new Promise((resolve, reject) => {
        axios.post(url, _data,{headers: {'Content-Type': 'multipart/form-data'},transformRequest: (data, headers) => _data}).then((response) => {
            const _res = JSON.parse(JSON.stringify(response.data));
            if(_res!=null){
                if(_res['code']===200||_res['status']==="success"){
                    return resolve(_res);
                }
                if(_res['code']===700101||_res['status']==="error"){
                    window.location.href ="/login"
                }
            }
            return reject(_res);
        }).catch((error) => {
            reject(error);
        });
    });
}

/**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */
export function patch(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.patch(url, data).then((response) => {
            resolve(response.data);
        },(err) => {
            reject(err);
        });
    });
}

/**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */
export function put(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.put(url, data).then((response) => {
            resolve(response.data);
        },(err) => {
            reject(err);
        });
    });
}

/*eslint no-extend-native: ["error", { "exceptions": ["Date"] }]*/
Object.defineProperty(Date.prototype,"Format",{
    value:function (fmt) {
        // "use strict";
        var o = {
          'M+': this.getMonth() + 1,
          'd+': this.getDate(),
          'H+': this.getHours(),
          'm+': this.getMinutes(),
          's+': this.getSeconds(),
          'S+': this.getMilliseconds()
        };
        //date.getFullYear()number,
        if (/(y+)/.test(fmt)) {
          //+date.getFullYear()+''number
          fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length));
        }
        for (var k in o) {
          if (new RegExp('(' + k + ')').test(fmt)) {
            //String()String(date.getFullYear())
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(String(o[k]).length)));
          }
        }
        return fmt;
      }
});


